import React, { useState } from "react";
import { Link } from "react-router-dom";

import "./Join.css";

const Join = (props) => {
  const [state_name, set_name] = useState();
  const [state_room, set_room] = useState();

  const changeNameHandler = (e) => {
    set_name(e.target.value);
  };

  const changeRoomHandler = (e) => {
    set_room(e.target.value);
  };

  return (
    <div className="join__container">
      <div className="join__container--inner">
        <h1 className="join__heading">Join</h1>
        <div className="join__form-container">
          <input
            placeholder="Tell me your name"
            className="join__input"
            type="text"
            onChange={changeNameHandler}
          />
          <input
            placeholder="Select the room"
            className="join__input mt-2"
            type="text"
            onChange={changeRoomHandler}
          />
          <Link
            to={`/chat?name=${state_name}&room=${state_room}`}
            onClick={(e) => (!state_room || !state_name) && e.preventDefault()}
          >
            <button className="join__button mt-2" type="submit">
              Sign In
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Join;
