import React from "react";

import "./Input.css";

const Input = (props) => {
  const { message, messageHandler } = props;
  const { enterHandler, sendMessage } = props;

  return (
    <form className="form">
      <input
        className="form__input"
        type="text"
        placeholder="Type some message ..."
        value={message}
        onChange={messageHandler}
        onKeyPress={enterHandler}
      />
      <button className="form__button" onClick={sendMessage}>
        Send
      </button>
    </form>
  );
};

export default Input;
