import React, { useState, useEffect } from "react";
import queryString from "query-string";
import io from "socket.io-client";

import InfoBar from "../InfoBar/InfoBar";
import Input from "../Input/Input";
import Messages from "../Messages/Messages";
import TextContainer from "../TextContainer/TextContainer";
import "./Chat.css";

let socket;

const Chat = (props) => {
  console.log("Render...");
  //React Router DOM
  const { search } = props.location;
  const [state_name, set_name] = useState("");
  const [state_room, set_room] = useState("");
  const [state_message, set_message] = useState("");
  const [state_messages, set_messages] = useState([]);
  const [state_users, set_users] = useState("");
  const END_POINT = "https://chat-app-yn.herokuapp.com/";

  //Send Message function.
  const sendMessage = (e) => {
    e.preventDefault();
    if (state_message) {
      socket.emit("sendMessage", state_message, () => {
        set_message("");
      });
    }
  };

  const messageHandler = (e) => {
    e.preventDefault();
    return set_message(e.target.value);
  };

  const enterHandler = (e) => {
    if (e.key === "Enter") return sendMessage(e);
    return;
  };

  useEffect(() => {
    const data = queryString.parse(search);
    const { name, room } = data;

    socket = io(END_POINT);
    socket.emit("join", { name: name, room: room }, (error) => {});

    set_room(room);
    set_name(name);

    return () => {
      socket.emit("disconnect");
      socket.off();
    };
  }, [set_room, set_name, search]);

  useEffect(() => {
    socket.on("message", (message) => {
      set_messages([...state_messages, message]);
    });

    socket.on("roomData", ({ users }) => {
      set_users(users);
    });
  }, [state_messages]);

  return (
    <div className="output__container">
      <div className="output__contianer--inner">
        <InfoBar room={state_room} />
        <Messages messages={state_messages} name={state_name} />
        <Input
          message={state_message}
          messageHandler={messageHandler}
          enterHandler={enterHandler}
          sendMessage={sendMessage}
        />
      </div>
      <TextContainer users={state_users} />
    </div>
  );
};

export default Chat;
