import React from "react";
import ReactEmoji from "react-emoji";

import "./Message.css";

const Message = (props) => {
  const { name } = props;
  const { text, user } = props.message;

  let isSendByCurrentUser = false;

  const trimName = name.trim().toLowerCase();

  if (user === trimName) {
    isSendByCurrentUser = true;
  }

  return isSendByCurrentUser ? (
    <div className="message__container justify-end">
      <p className="message__send">{name}</p>
      {text && (
        <div className="message__box">
          <p className="message__text">{ReactEmoji.emojify(text)}</p>
        </div>
      )}
    </div>
  ) : (
    <div className="message__container justify-start">
      {text && (
        <div className="message__box">
          <p className="message__text">{ReactEmoji.emojify(text)}</p>
        </div>
      )}
      <p className="message__send">{user}</p>
    </div>
  );
};

export default Message;
